<template>
  <canvas id="a_canvas" 
  :height="canvasHeight" 
  :width="canvasWidth" 
  :style="{'border-top':'1px solid' + borderc,'border-left':'1px solid'+borderc}" 
  @click="coordinate">
</canvas>
</template>
<script>
export default {   
  name:'TableCanvas',
  data(){
    return {
      a_canvas:null,
      context:null,
      imgObj:null,
      cell_width:0,
      cell_height:0,
    }
  },
  props:{
    canvasHeight:{
        type:Number,
        default:400,
    },
    canvasWidth:{
        type:Number,
        default:400,
    },
    grid_cols:{
        type:Number,
        default:10,
    },
    grid_rows:{
        type:Number,
        default:10,
    },
    borderc:{
        type:String,
        default:"#000",
    },
    selectc:{
        type:String,
        default:'#000',
    },
    canvasImg:{
        type:String,
        default:'',
    }
  },
  methods:{
    imgload(){
        const vm = this;
        const {
            context,
            drawtable,
            canvasHeight,
            canvasWidth,
            canvasImg,
        } = this;
        context.clearRect(0,0,canvasWidth,canvasHeight);
        this.imgObj = new Image();
        this.imgObj.src = canvasImg;
        this.imgObj.onload = function(){
            vm.drawtable(this)
        }
    },
    coordinate(e){
        const {
            cell_width,
            cell_height,
            context,
            drawtable,
            imgObj,
            canvasHeight,
            canvasWidth,
        } = this;
        const {
            layerX,
            layerY
        } = e;//canvas
        var x_line = Math.floor(layerX/cell_width);
        var y_line = Math.floor(layerY/cell_height);
        context.clearRect(0,0,canvasWidth,canvasHeight);
        drawtable(imgObj);
        context.fillStyle = this.selectc;
        context.fillRect(x_line*cell_width,y_line*cell_height,cell_width,cell_height);
        this.$emit('click',[x_line,y_line],e);
    },
    drawtable(img){
        const {
            a_canvas,
            imgObj,
            context,
            canvasHeight,
            canvasWidth,
            grid_cols,
            grid_rows,
        } = this;
        
        if(imgObj.width >= imgObj.height){
            var imgH = imgObj.height*canvasHeight/imgObj.width;
            var top = (canvasHeight-imgH)/2;
            context.drawImage(imgObj, 0, top, canvasWidth, imgH);
        }else{
            var imgW  = imgObj.width*canvasWidth/imgObj.height;
            var left = (canvasWidth - imgW)/2;
            context.drawImage(imgObj, left, 0, imgW, canvasHeight);
        }
        context.fillStyle = "rgba(255,255,255,0)";
        context.fillRect(0,0,a_canvas.width,a_canvas.height);
        // 
        this.cell_height = a_canvas.height / grid_rows;
        this.cell_width = a_canvas.width / grid_cols;
        context.lineWidth = 1;
        context.strokeStyle = this.borderc;
        // 
        context.beginPath();
        // 
        for (var col = 0; col <= grid_cols; col++) {
        var x = Math.ceil(col * this.cell_width);
            context.moveTo(x-0.5,0); 
            context.lineTo(x-0.5,a_canvas.height);
        }
        // 
        for(var row = 0; row <= grid_rows; row++){
            var y = Math.ceil(row * this.cell_height);
            context.moveTo(0,y-0.5);
            context.lineTo(a_canvas.width, y-0.5);
        }
        //  linewidth =   0.5 1 0.5
        context.stroke();
    },
    
  },    
  mounted(){
    this.a_canvas = document.getElementById('a_canvas');
    this.context = this.a_canvas.getContext("2d");
    this.imgload();
  },
  watch:{
      canvasImg(val,oldval){
          if(val != oldval){
              this.imgload();
          }
      }
  },
}
// canvasHeight="400" 
// canvasWidth="400" 
// grid_cols="10"  
// grid_rows="10" 
// borderc="#ff5000" 
// selectc="#ff5000" 
// canvasImg="img" 
</script>




